<template>
    <div id="mall-basic">
        <public-header title="基础设置"></public-header>
        <div class="content">
            <van-cell-group>
                <van-cell>
                    <div slot="title">跑腿状态</div>
                    <iswitch :value="mall.status" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="mall.status"></iswitch>
                </van-cell>
                <van-field label="跑腿关闭原因" v-model="mall.close_reason" placeholder="请输入跑腿关闭原因" input-align="right" />
            </van-cell-group>
            <van-cell-group class="margin-10-t">
                <van-cell class="border-0px">
                    <div slot="title">跑腿服务半径</div>
                    <div slot="right-icon">
                        <div class="input-unit">
                            <input type="number" v-model="mall.serve_radius"><span>KM</span>
                        </div>
                    </div>
                </van-cell>
                <van-field label="跑腿服务城市" v-model="mall.city" placeholder="请输入跑腿服务城市" input-align="right" />
                <van-field label="客服电话" v-model="mall.mobile" placeholder="请输入客服电话" input-align="right" />
            </van-cell-group>
            <div class="padding-15">
                <van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
            </div>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				mall: {
					status: '1',
					close_reason: '',
					serve_radius: '0',
					mobile: '',
                }
			}
		},
		components: {
			publicHeader,
            iswitch,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'mall',
					url: 'plateform/errander/config/basic'
				});
			},
			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},
			onSubmit(){
				var that = this;
				console.log(that.mall);
				setData({
					vue: this,
					url: 'plateform/errander/config/basic',
					data: that.mall
				});
			}
		},
        mounted(){
			this.onLoad();
        }
	}
</script>

<style>

</style>


